.btn-new {
  display: inline-block;
  background-color: #6741d9;
  color: #fff;
  height: 80px;
  font-size: 30px;
  font-weight: 500;
  border: 2px solid #fff;
  padding: 10px 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-new:hover {
  background-color: #5f3dc4;
}
