.search-container {
  display: flex;
  gap: 10px;
}

.search-input {
  font-size: 30px;
  padding-left: 20px;
  background-color: #6741d9;
  width: 700px;
  height: 70px;
  border: none;
  border: 2px solid #fff;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.btn-search {
  font-size: 30px;
  color: #fff;
  background-color: #6741d9;
  width: 150px;
  height: 70px;
  border: 2px solid #fff;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-search:hover {
  background-color: #5f3dc4;
}
